header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
  font-family: "Indie Flower", cursive;
  font-size: 24px;
  text-align: center;
  color: #023d13;
  font-weight: 900;
  background-image: url(https://img.freepik.com/vector-gratis/fondo-floral-acuarela_125540-829.jpg?w=2000); }

input {
  border-radius: 40px;
  border: none;
  width: 250px;
  height: 20px; }

textarea:focus,
input:focus,
input[type]:focus {
  border-color: #24cb74;
  box-shadow: 0 1px 1px rgba(229, 103, 23, 0.075) inset, 0 0 8px #24cb74;
  outline: 0 none; }

main {
  background-color: #b6edbd;
  width: 100%;
  font-family: "Indie Flower", cursive;
  font-size: 20px;
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 35px;
  color: #023d13; }

.recipes .choosen {
  margin-left: 10%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 25px;
  grid-row-gap: 45px; }

.recipe {
  background-image: url(https://img.freepik.com/vector-gratis/fondo-floral-acuarela_125540-829.jpg?w=2000);
  width: 80%;
  box-sizing: border-box;
  border-radius: 40px;
  margin-bottom: 20px;
  margin-right: auto;
  font-weight: 900;
  box-shadow: 0px 5px 5px rgba(5, 110, 56, 0.5); }

.image {
  border-radius: 10px;
  width: 150px; }

/*.calories {
  color: rgb(255, 128, 0);
}*/
.recoverbutton, .savebutton {
  width: 150px;
  height: 35px;
  background: #e5eb94;
  background-image: -webkit-linear-gradient(top, #e5eb94, #20b076);
  background-image: -moz-linear-gradient(top, #e5eb94, #20b076);
  background-image: -ms-linear-gradient(top, #e5eb94, #20b076);
  background-image: -o-linear-gradient(top, #e5eb94, #20b076);
  background-image: linear-gradient(to bottom, #e5eb94, #20b076);
  border: none;
  border-radius: 11px;
  text-shadow: 1px 1px 1px #eef158;
  -webkit-box-shadow: 6px 5px 24px #074326;
  -moz-box-shadow: 6px 5px 24px #074326;
  box-shadow: 6px 5px 24px #074326;
  font-family: "Indie Flower", cursive;
  color: #023d13;
  font-size: 18px; }

footer {
  width: 100%;
  background-image: url(https://img.freepik.com/vector-gratis/fondo-floral-acuarela_125540-829.jpg?w=2000);
  padding-top: 30px;
  padding-bottom: 30px;
  font-family: "Indie Flower", cursive;
  font-size: 18px;
  text-align: center;
  color: #023d13; }
